<template>
    <div style="width:90%;">
        <div class="mb-6">
            <h2 >{{ convertirNumeroADias(info.dia) + ' - ' + info.nombre }}</h2>
            <p >{{ info.descripcion }}</p>
        </div>
        <div class="mt-2">
            <v-card
            class="mb-1"
            v-if="info.ejerciciosList"
            v-for="(ejer, index) in info.ejerciciosList"
            :key="ejer.id">
                <v-card-title>
                    <h3>{{ ejer.nombre }}</h3>
                </v-card-title>
                <v-card-text>
                    <h4>{{ ejer.descripcion }}</h4>
                    <h4>Series: {{ ejer.series }}</h4>
                    <h4>Repeticiones: {{ ejer.repeticiones }}</h4>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-icon class="ma-2" @click="accionModificar(index)" color="green" v-if="condicion">mdi-pencil</v-icon>
                    <v-icon class="ma-2" @click="accionBorrar(index)" color="red" v-if="condicion">mdi-delete</v-icon>
                    <v-checkbox class="pr-4" label="Finalizar Ejercicio" v-if="!condicion"></v-checkbox>
                </v-card-actions>
            </v-card>
        </div>
        <div class="d-flex justify-start mt-6">
            <v-btn @click="this.$router.back()" >
                <v-icon>mdi-reply</v-icon>
                <span>Volver</span>
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn  @click="dialog = true" v-if="condicion">
                <v-icon>mdi-plus</v-icon>
                <span>Agregar ejercicio</span>
            </v-btn>
        </div>
        <v-dialog 
        v-model="dialog">
            <v-card class="pa-4">
                <v-form class="pa-4">
                    <v-row>
                        <v-autocomplete
                        label="Seleccionar Ejercicio"
                        :items="this.$store.getters.listadoEjercicios"
                        @blur="obtenerDescripcion"
                        v-model="ejercicioTemplate.nombre">
                        </v-autocomplete>
                    </v-row>
                    <v-row>
                        <v-text-field
                        label="Descripcion"
                        v-model="ejercicioTemplate.descripcion">
                        </v-text-field>
                    </v-row>
                    <v-row>
                        <v-text-field
                        label="Series"
                        v-model="ejercicioTemplate.series">    
                        </v-text-field>
                    </v-row>
                    <v-row>
                        <v-text-field
                        label="Repeticiones"
                        v-model="ejercicioTemplate.repeticiones">    
                        </v-text-field>
                    </v-row>
                </v-form>
                <div>
                    <v-row class="pa-4">
                        <v-btn @click="cerrarDialog" >
                            <v-icon>mdi-close-box</v-icon>
                            Cerrar
                        </v-btn>
                        <v-spacer></v-spacer>
                        <v-btn @click="accionGuardar" >
                            <v-icon>mdi-content-save-edit</v-icon>
                            Guardar
                        </v-btn>
                    </v-row>
                </div>
            </v-card>
        </v-dialog>
    </div>
  </template>
  
  <script>
  import utiles from '../utiles/funciones';
  
  export default {
    data() {
      return {
        dialog: false,
        info: this.$store.getters.planEspecifico(
          this.$route.params.idPlanSemanal,
          utiles.convertirDiaANumero(this.$route.params.dia)
        ),
        ejercicioTemplate: {
          id: 0,
          nombre: '',
          descripcion: '',
          series: 0,
          repeticiones: 0
        },
      }
    },
    methods: {
      accionModificar(index) {
        this.ejercicioTemplate = this.info.ejerciciosList[index];
        this.dialog = true;
      },
      accionBorrar(indexEjercicio) {
        this.$store.commit('eliminarEjercicio', {
          idSemanal: parseInt(this.$route.params.idPlanSemanal),
          indexEjercicio: indexEjercicio,
          dia: this.info.dia
        });
      },
      accionGuardar() {
        this.$store.commit('guardarEjercicio', {
          template: this.ejercicioTemplate,
          idSemanal: parseInt(this.$route.params.idPlanSemanal),
          dia: this.info.dia
        });
        this.cerrarDialog();
      },
      obtenerDescripcion() {
        const index = this.$store.getters.listadoEjercicios.indexOf(this.ejercicioTemplate.nombre);
        this.ejercicioTemplate.descripcion = this.$store.getters.descripcionEspecifica(index);
      },
      cerrarDialog() {
        this.dialog = false;
        this.ejercicioTemplate = {
          id: 0,
          nombre: '',
          descripcion: '',
          series: 0,
          repeticiones: 0
        };
      },
      convertirNumeroADias(diaNum) {
        return utiles.convertirNumeroADias(diaNum);
      },
      convertirDiaANumero(dia) {
        return utiles.convertirDiaANumero(dia);
      },
      usuarioCliente(){
            
            return false
        }
    },
    computed: {
        condicion(){
            return (this.$store.getters.tipoUsuario == 1 ? 
            true
            :
            this.usuarioCliente()
        )
        }
  }
}
  </script>
  